<template>
  <div id="home">
    <NavBar class="home-nav">
      <template #center></template>
    </NavBar>
    <TabControl
        :titles="['', '', '']"
        ref="tabcontrol1"
        class="fixed"
        @tabClick="tabClick"
        v-show="istabcontrolshow"
      ></TabControl>
    <Scroll
      class="home-content"
      ref="scroll"
      :probeType="3"
      @contentposition="contentposition"
      :pullup="true"
      @pullup="loadmore"
    >
      <HomeSwiper :banners="banners" @swiperimgloaded.once="swiperimgloaded">
      </HomeSwiper>
      <HomeRecommendView :recommends="recommends"></HomeRecommendView>
      <HomeFeature></HomeFeature>
      <TabControl
        :titles="['', '', '']"
        ref="tabcontrol2"
        @tabClick="tabClick"
      ></TabControl>
      <GoodsList :goods="showGoods"> </GoodsList>
    </Scroll>
    <BackTop @click.native="backtopClick" v-show="isbacktopshow"></BackTop>
  </div>
</template>

<script>
import NavBar from "common/navbar/NavBar.vue";
import Scroll from "common/scroll/Scroll.vue";
import TabControl from "content/tabControl/TabControl.vue";
import GoodsList from "content/goods/GoodsList.vue";
import GoodsListItem from "content/goods/GoodsListItem.vue";
import BackTop from "content/backtop/BackTop.vue";

import HomeSwiper from "./childComps/HomeSwiper";
import HomeRecommendView from "./childComps/HomeRecommendView";
import HomeFeature from "./childComps/HomeFeature";

import { getHomeMultidata, getHomeGoods } from "network/home.js";
// import { debounce } from "@/common/utils.js";
export default {
  name: "Home",
  components: {
    NavBar,
    HomeSwiper,
    HomeRecommendView,
    HomeFeature,
    TabControl,
    GoodsList,
    GoodsListItem,
    Scroll,
    BackTop,
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop",
      isbacktopshow: false,
      taboffsetTop: 0,
      istabcontrolshow: false,
      saveY:0
    };
  },
  computed: {
    showGoods() {
      return this.goods[this.currentType].list;
    },
  },
  // home router-view keep-alive
  // 
  activated(){  
    console.log(this.saveY);
    this.$refs.scroll.refresh()
    this.$refs.scroll.scrollTo(0,this.saveY,0)
  },
  deactivated(){
    this.saveY = this.$refs.scroll.scroll.y;
    console.log(this.saveY);
  },
  // 
  created() {
    this.getHomeMultidata();
    this.getHomeGoods("pop");
    this.getHomeGoods("new");
    this.getHomeGoods("sell");
  },
  updated() {
    // offsetTop-- mountedupdated()
    this.$nextTick(() => {
       this.taboffsetTop = this.$refs.tabcontrol2.$el.offsetTop;
    });
  },
  methods: {
    /**
     * 
     */
    tabClick(index) {
      // console.log(index);
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      // currentIndex--->data
      this.$refs.tabcontrol1.currentIndex = index;
      this.$refs.tabcontrol2.currentIndex = index;
    },
    backtopClick() {
      // backTop()
      this.$refs.scroll.scrollTo(0,0,500);
    },
    contentposition(position) {
      // backtop
      this.isbacktopshow = position.y < -200 
      // tabcontrol
      this.istabcontrolshow = position.y < -this.taboffsetTop
    },
    loadmore() {
      this.getHomeGoods(this.currentType);
    },
    swiperimgloaded() {
      // 
      // updated
      // console.log(this.$refs.tabcontrol.$el.offsetTop)
      // this.taboffsetTop = this.$refs.tabcontrol2.$el.offsetTop;
    },
    /**
     * 
     */
    getHomeMultidata() {
      getHomeMultidata().then((value) => {
        // console.log(value);
        this.banners = value.data.data.banner.list;
        this.recommends = value.data.data.recommend.list;
      });
    },
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then((value) => {
        // console.log(value.data.data.list);
        this.goods[type].list.push(...value.data.data.list);
        this.goods[type].page += 1;
        this.$refs.scroll.refresh();
      });
    },
  },
};
</script>

<style scoped>
/* scoped */
#home {
  height: 100vh;
}
.home-nav {
  background-color: var(--color-tint);
  color: white;
}

.fixed {
  position: relative;
  z-index: 10;
}
.home-content {
  overflow: hidden;
  position: absolute;
  top: 44px;
  bottom: 10px;
  left: 0;
  right: 0;
}
</style>